// 
import config from "./urlconfig.js";
import User from "./user.js";
export default {
	//
	config: {
		baseUrl: config.webUrl,
		v2url: config.v2Url,
		v1Url: config.v1Url,
		header: {
			'Content-Type': 'application/x-www-form-urlencoded',
			'appid': 'a',
		},
		data: {},
		method: "GET",
		dataType: "json",
	},
	request(options = {}) {
		options.header = options.header || this.config.header; //options.header=options.header || 
		options.method = options.method || this.config.method;
		options.dataType = options.dataType || this.config.dataType;
		options.url = this.config.v1Url + options.url;
		// TODOtoken,,	
		return uni.request(options).then(data => {
			let [err, res] = data;
			if (!User.eck(err)) {
				return null;
			}
			
			if (res.data.status != 0) {
				let msg=res.data.msg;
				
				if (msg === '' || msg === ',') {
					uni.showToast({
						title: msg,
						icon: "none"
					});
					uni.navigateTo({
						url: '/pages/login/login'
					});
				} else if (msg === '') {
					uni.showToast({
						title: msg,
						icon: "none"
					});
					uni.switchTab({
						url: '/pages/index/index'
					});
				}
				else{
					uni.showToast({
						title: msg,
						icon: "none"
					});
				}
			}
			return res.data;
		});
	},

	get(url, data, options = {}) {
		options.url = url;
		options.data = data;
		options.method = 'GET';
		options.header = {
			'Content-Type': 'application/x-www-form-urlencoded',
			'appid': 'a',
			'dian_token': User.token
		};
		return this.request(options);
	},
	post(url, data, options = {}) {
		options.url = url;
		options.data = data;
		options.method = 'POST';
		options.header = {
			'Content-Type': 'application/json;charset=UTF-8',
			'appid': 'a',
			'dian_token': User.token
		};
		return this.request(options);
	},
	/***/
	requestV2(options = {}) {
		options.header = options.header || this.config.header; //options.header=options.header || 
		options.method = options.method || this.config.method;
		options.dataType = options.dataType || this.config.dataType;
		options.url = this.config.v2url + options.url;
		// TODOtoken,,
		return uni.request(options);
	},
	getV2(url, data, options = {}) {
		options.url = url;
		options.data = data;
		options.method = 'GET';
		options.header = {
			'Content-Type': 'application/x-www-form-urlencoded',
			'appid': 'a',
			'dian_token': User.token
		};
		return this.requestV2(options);
	},
	postV2(url, data, options = {}) {
		options.url = url;
		options.data = data;
		options.method = 'POST';
		options.header = {
			'Content-Type': 'application/json;charset=UTF-8',
			'appid': 'a',
			'dian_token': User.token
		};
		return this.requestV2(options);
	},
	/***/
	// 
	upload(url, options = {}) {
		options.url = this.config.baseUrl + url;
		options.header = options.header || this.config.header;
		options.fileType = options.fileType || "image";
		options.formData = options.formData || {};
		options.filePath = options.filePath;
		options.name = options.name;
		// TODOtoken
		if (options.token) {
			// 
			if (!this.checkToken(options.checkToken)) return;
			// 
			if (!this.checkAuth(options.checkAuth)) return;
			options.header.token = User.token;
		}

		return uni.uploadFile(options);
	},

	// 
	errorCheck(err, res, errfun = false, resfun = false) {
		if (err) {
			typeof errfun === 'function' && errfun();
			uni.showToast({
				title: '',
				icon: "none"
			});
			return false;
		}
		if (res.data.errorCode) {
			typeof errfun === 'function' && resfun();
			uni.showToast({
				title: res.data.msg,
				icon: "none"
			});
			return false;
		}
		return true;
	},



	// User.token  false 
	checkToken(checkToken) {
		if (checkToken && !User.token) {
			uni.showToast({
				title: '',
				icon: "none"
			})
			uni.navigateTo({
				url: '/pages/login/login'
			});
			return false;
		}
		return true;
	},
	// 
	checkAuth(checkAuth) {
		if (checkAuth && !User.userinfo.phone) {
			uni.showToast({
				title: '',
				icon: "none"
			})
			uni.navigateTo({
				url: '/pages/user-bind-phone/user-bind-phone'
			});
			return false;
		}
		return true;
	},
	//uuid
	getUuid(checkAuth) {
		return Date.parse(new Date());
	}
}
